'use strict';

import React from 'react';
import classNames from 'classnames';
import $ from 'jquery';

import css from '../stylesheets/uploadcontainer.css';

export default class UploadButton extends React.Component {
   /**
      * Base Button
      * @param  {Object} props
   */
   constructor(props) {
      super(props);
      this.state = {
         buttonLabel: ''
      };
      this.handleChange = this.handleChange.bind(this);
      this.onClickHandler = this.onClickHandler.bind(this);
   }

   /**
      * Handle the button text label after component rendered
   */
   componentDidMount () {
      let $this = $(this.myTextInput),
         label = this.props.label;
      $this.attr('data-content', label);
   }

   /**
      * Handle the button state
      * @param  {Event]} event
   */
   handleChange (e) {
      this.setState({
         buttonLabel: e.target.value.split(/(\\|\/)/g).pop()
      });
      if (this.props.onChange) {
         this.props.onChange(e);
      }
   }

   onClickHandler (e) {
      e.preventDefault();

   //    let $rippleElement = $('<span class="ripple-effect" />'),
   //    $buttonElement = $(this),
   //    btnOffset = $buttonElement.offset(),
   //    xPos = e.pageX - btnOffset.left,
   //    yPos = e.pageY - btnOffset.top,
   //    size = parseInt(Math.min($buttonElement.height(), $buttonElement.width()) * 0.5),
   //    animateSize = parseInt(Math.max($buttonElement.width(), $buttonElement.height()) * Math.PI);

   // $rippleElement
   //    .css({
   //       top: yPos,
   //       left: xPos,
   //       width: size,
   //       height: size,

   //       backgroundColor: $buttonElement.data('ripple-color')
   //    })
   //    .appendTo($buttonElement)
   //    .animate({
   //       width: animateSize,
   //       height: animateSize,
   //       opacity: 0
   //    }, 700, function() {
   //       $(this).remove();
   //    });

      // Trigger a click event on input file
      this.refs.myTextInput.click();
   }

   render() {
      // let text = this.props.store.submissionInProgress ? 'Processing...' : 'Submit';
      let { buttonStyle } = this.props;

      let btnClasses = classNames(
         css.inputHidden,
         css[buttonStyle],
         css.ripple,
         css.btn
      );
      // Construct props
      let inputProps = {
         disabled: this.props.disabled,
         ref:'myTextInput',
         className: btnClasses,
         accept: this.props.accept,
         onChange: this.handleChange,
         onClick: this.handleClick
      };

      console.log(this.props)

      return (
         <div>
            <input type='file'
               {...inputProps}
            />
            <button
               className={btnClasses}
               onClick={this.onClickHandler}>
               {this.props.buttonLabel || 'Upload'}
            </button>
         </div>
      );
   }
}

/**
 * Type Checks
 * @type {Object}
 */
const { string, func } = React.PropTypes;

UploadButton.propTypes = {
   accept: string.isRequired,
   color: string,
   label: string,
   onChange: func
};



